import { useState } from 'react';
import Flutterwave from 'flutterwave-node-v3'
import './App.css';


function App() {
  const [amount, setAmount] = useState(0);
  const flw = new Flutterwave("FLWPUBK_TEST-0def6bc31ec985e3e7f3985696339e2e-X", "FLWSECK_TEST-3198124bce13338d9fbc55e37cfe9f22-X");
  const rw_mobile_money = async () => {

    try {

      const payload = {
        "tx_ref": "MC-158523s09v5050e8", //This is a unique reference, unique to the particular transaction being carried out. It is generated when it is not provided by the merchant for every transaction.
        "order_id": "USS_URG_893982923s2323", //Unique ref for the mobilemoney transaction to be provided by the merchant
        "amount": "0",
        "currency": "RWF",
        "email": "olufemi@flw.com",
        "phone_number": "0788804581",
        "fullname": "Rene Uwumuhire"
      }

      const response = await flw.MobileMoney.rwanda(payload)
      console.log(response);
    } catch (error) {
      console.log(error)
    }

  }



  const onSubmit = (e) => {
    e.preventDefault();
    setAmount(amount + 1);
    rw_mobile_money();

  };
  const onAmountChange = (e) => {
    setAmount(e.target.value);
  };

  return (
    <div className="App">
      <form onSubmit={onSubmit}>
        <button type="submit"> Submit </button>
      </form>
    </div>
  );
}

export default App;
